.bottomBorder {
  border-radius: 0 0 2rem 2rem;
}

.special-text {
  font-family: "Edu NSW ACT Cursive", cursive;
  font-weight: bold;
}

html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  background-color: #f5eedd;
  overflow-x: hidden;
}

h1 {
  font-family: "PT Sans", sans-serif;
  font-weight: bold;
}

h2 {
  font-family: "PT Sans", sans-serif;
  font-size: 30px;
}

p {
  font-family: "Poppins", sans-serif;
  font-weight: normal;
  font-size: 22px;
}

a {
  font-family: "PT Sans", sans-serif;
  color: black;
  text-decoration: none;
}

.mainContainer {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.linkHover {
  transition: 0.3s;
}
.linkHover:hover {
  color: #017b7e;
}

.mobileTitle {
  display: none;
}

header {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
}

.headerTitle {
  transition: transform 0.6s ease, opacity 0.6s ease;
}
.headerTitle.hide {
  transform: translateX(100px) scale(0.8);
  opacity: 0;
  z-index: 0;
}

.profileWrap .sideText {
  opacity: 0;
  pointer-events: none;
  font-family: "Edu NSW ACT Cursive", cursive;
  font-size: clamp(1rem, 3vw, 1.5rem);
  font-weight: bold;
  position: absolute;
  top: 90%;
  transform: translateY(-50%);
  transition: opacity 0.5s ease;
}
.profileWrap .sideText.left {
  left: -8vw;
}
.profileWrap .sideText.right {
  right: -12vw;
}

.navegacionContainer {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  background-color: #011a27;
  min-width: 100%;
  padding: 2rem 0;
  height: 420px;
  position: relative;
  z-index: 1;
}
.navegacionContainer .navegacion,
.navegacionContainer .conoceme,
.navegacionContainer .down-arrow {
  transition: transform 0.6s ease;
  transform: translateY(100px);
}

.navegacion {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 0 5rem;
  height: 8.5rem;
}
.navegacion .info {
  display: flex;
  align-items: center;
  gap: 4rem;
}
.navegacion .title {
  display: flex;
  text-align: center;
}
.navegacion .title__down {
  font-family: "Edu NSW ACT Cursive", cursive;
  color: #f5eedd;
  font-size: 30px;
  font-weight: lighter;
  margin: 0 0 1rem 0;
}
.navegacion a {
  text-decoration: none;
  font-weight: bold;
  color: #f5eedd;
}
.navegacion a :hover {
  color: #017b7e;
}

.profileWrap:hover ~ .navegacionContainer .navegacion,
.profileWrap:hover ~ .navegacionContainer .conoceme {
  transform: translateY(0);
}

.profileWrap:hover ~ .navegacionContainer .down-arrow {
  transform: translateY(0);
}

.main {
  text-align: center;
}
.main h2 {
  margin-bottom: 0.5rem;
}
.main p {
  margin: 0.5rem 0 2rem 0;
  padding: 0 15rem;
}
.main__habilidades {
  margin-bottom: 1.5rem;
}
.main__sobreMi {
  color: #f5eedd;
  background-color: #017b7e;
}

/* Base style for the arrow */
.down-arrow {
  width: 30px;
  height: 30px;
  display: none;
  animation: bounce 1.5s infinite;
}
.down-arrow svg {
  width: 100%;
  height: 100%;
  fill: white;
}

/* Bounce animation */
@keyframes bounce {
  0%, 20%, 50%, 80%, 100% {
    transform: translateY(0);
  }
  40% {
    transform: translateY(8px);
  }
  60% {
    transform: translateY(4px);
  }
}
/* Mobile only */
@media (max-width: 480px) {
  .down-arrow {
    display: block;
  }
}
.siteContent {
  background: #111;
  color: white;
  padding: 20px;
  margin-top: 20px;
  transition: transform 0.8s ease-in-out;
}

.smoothTransform {
  transition: transform 0.8s cubic-bezier(0.25, 1, 0.5, 1);
}

.profileWrap {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column-reverse;
  margin: 2rem 0;
}
.profileWrap .flip-card {
  width: 300px;
  height: 300px;
  perspective: 1000px;
  position: relative;
  z-index: 2;
  margin-bottom: -150px;
}
.profileWrap .flip-card .flip-card-inner {
  position: relative;
  width: 100%;
  height: 100%;
  transform-style: preserve-3d;
  transition: transform 0.8s ease;
}
.profileWrap .flip-card .flip-card-inner:hover {
  transform: rotateY(180deg);
}
.profileWrap .flip-card:hover .flip-card-inner {
  transform: rotateY(180deg);
}
.profileWrap .flip-card .flip-card-front,
.profileWrap .flip-card .flip-card-back {
  position: absolute;
  width: 100%;
  height: 100%;
  backface-visibility: hidden;
  border-radius: 100%;
  overflow: hidden;
  top: 0;
  left: 0;
}
.profileWrap .flip-card .flip-card-front img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.profileWrap .flip-card .flip-card-back {
  background: #f5eedd;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  transform: rotateY(180deg);
  gap: 10px;
}
.profileWrap .flip-card .flip-card-back img {
  width: max-content;
  height: max-content;
}

/* When hover happens on profileWrap → trigger page changes */
.profileWrap:hover ~ .navegacionContainer {
  transform: translateY(120px);
  transition: transform 0.5s ease;
}
.profileWrap:hover .sideText {
  opacity: 1;
  pointer-events: auto;
}
.profileWrap:hover ~ .mobileTitle {
  opacity: 1;
  pointer-events: auto;
}
.profileWrap:hover ~ .headerTitle h1 {
  opacity: 0;
  transform: translateY(-20px);
}

/* Responsive layout */
@media (max-width: 768px) {
  .mobileTitle {
    display: none;
  }
  .navegacion {
    padding: 0 2rem;
  }
  .navegacion .title__down {
    font-size: clamp(1.5rem, 4vw, 1.6rem);
  }
  .flip-card {
    width: 200px;
    height: 200px;
  }
}
@media (max-width: 480px) {
  .navegacionContainer {
    justify-content: space-between;
  }
  .mobileTitle {
    display: block;
    position: relative;
    top: 100px;
    z-index: 1000;
    opacity: 0;
    transition: opacity 0.5s ease;
    font-size: 1.5rem;
  }
  .navegacion {
    margin-top: 2rem;
    padding: 0 1rem;
  }
  .navegacion .info {
    margin-bottom: 1rem;
    flex-direction: column;
    gap: 2rem;
  }
  .navegacion .info p {
    margin: 0;
  }
  .flip-card {
    width: 160px;
    height: 160px;
    margin-bottom: -80px;
  }
  .profileWrap .sideText {
    display: none;
  }
  .conoceme {
    display: none;
  }
}
@media (max-width: 768px) {
  .main p:not(.desc) {
    padding: 0.2rem;
  }
  .main__habilidades .card {
    flex-direction: column;
    width: 100%;
  }
  .main__habilidades .card span {
    transform: none;
  }
  .main__habilidades .card span {
    width: 100%;
  }
  .main__habilidades .card__content:hover {
    flex: 30;
  }
}